



























































import Vue from "vue";

export default Vue.extend({
  name: "Appbar",
  data: () => ({
    loading: false,
    items: [] as string[],
    search: null,
    select: null,
    states: ["Alabama"],
    isSearching: false,
    isFocused: false
  }),
  watch: {
    search(val) {
      val && val !== this.select && this.querySelections(val);
    }
  },
  computed: {
    state() {
      switch (this.$route.name) {
        case "dashboard":
          return "MainSearch";
        case "login":
          return "OpenPage";
        case "signup":
          return "OpenPage";
        default:
          return "MainSearch";
      }
    },
    title() {
      switch (this.$route.name) {
        case "dashboard":
          return "OpenBaaS";
        case "login":
          return "Log in";
        case "signup":
          return "Sign up";
        default:
          return "";
      }
    },
    navIcon() {
      switch (this.$route.name) {
        case "dashboard":
          return "$mdiMenu";
        default:
          return "$mdiArrowLeft";
      }
    },
    showNavbtn() {
      if (this.$vuetify.breakpoint.mobile) {
        return true;
      }
      return false;
    },
    showTitle() {
      if (this.$vuetify.breakpoint.mobile) {
        switch (this.$route.name) {
          case "dashboard":
            return false;
          case "login":
            return true;
          case "signup":
            return true;
          default:
            return false;
        }
      } else return true;
    },
    showSearchbar() {
      if (this.$vuetify.breakpoint.mobile) {
        switch (this.$route.name) {
          case "dashboard":
            return true;
          default:
            return false;
        }
      } else {
        switch (this.$route.name) {
          case "dashboard":
            return true;
          default:
            return false;
        }
      }
    }
  },
  methods: {
    querySelections(v: string) {
      this.loading = true;
      // Simulated ajax query
      setTimeout(() => {
        this.items = this.states.filter(e => {
          return (e || "").toLowerCase().indexOf((v || "").toLowerCase()) > -1;
        }) as string[];
        this.loading = false;
      }, 500);
    },
    onClickUser() {
      this.$router.push("/login");
      console.log(this.$route.name);
    },
    onClickNav() {
      if (this.$route.name === "dashboard") this.$store.commit("toggleDrawer");
      else if (this.$route.name === "login") this.$router.back();
      else this.$router.back();
    },
    onFocus() {
      this.isFocused = true;
    },
    onBlur() {
      this.isFocused = false;
    }
  }
});
